import React, { Component } from 'react'
import Input from './Input'

export class Dialogue extends Component {
    render() {
        return (
            <div id="dialogue-box">
                <p id="dialogue-text">
                    {messages[this.props.dialogueIndex]}
                </p>
                <img style={{ cursor: 'pointer' }} 
                    onClick={this.props.onClick}  
                    src="https://www.shareicon.net/data/256x256/2015/11/01/665144_arrows_512x512.png" 
                    alt=""
                />
                {this.props.dialogueIndex === 2 && <Input onSubmit={this.props.onSubmit}/>}
                <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="708px" height="212px">
                    <image x="0px" y="0px" width="708px" height="212px"
                        xlinkHref="data:img/png;base64," />
                </svg>
            </div>
        )
    }
}



